// @flow
import classNames from 'classnames';
import { get, has, last } from 'lodash';
import { DateTime } from 'luxon';
import Ansi from 'ansi-to-react';
import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { compose, withHandlers, withState } from 'recompose';
import { reduxForm, SubmissionError } from 'redux-form';

// Components
import Button from 'components/Button';
import Form, { Input } from 'components/Form';
import Modal from 'components/Modal';

// Ducks
import {
  VALIDATOR_CREATE_MODAL_ID,
  VALIDATOR_NODE_FORM_ID,
  VALIDATOR_NODE_MODAL_ID,
  VALIDATOR_NODE_FROM_MODAL_ID,
} from '../ducks';

// Services
import { closeModal, openModal } from 'services/modals';

// Styles
import { COLOR, GRADIENT, Typography } from 'styles';
import styles from './Common.scss';

// Utils
import validate, { isHost, isNumber, max, min, required } from 'utils/validate';

const ValidatorsNodeForm: React.Element<Form> = ({
  error,
  handleSubmit,
  isFrom,
  onCancel,
  submittin,
}) => (
  <Form
    error={error}
    onSubmit={handleSubmit}
  >
    <Input label="" name="host" placeholder="localhost" />
    <Input label="" name="port" placeholder="22" />
    <Input label=" " name="username" placeholder="root" />
    <Input label="" name="password" type="password" />

    <div className={styles.Actions}>
      <Button
        color={COLOR.SECONDARY}
        onClick={onCancel}
      >
        
      </Button>

      <Button
        color={isFrom ? GRADIENT.GREEN : GRADIENT.PURPLE}
        type="submit"
      >
        {isFrom ? '' : ''}
      </Button>
    </div>
  </Form>
);

const ComposedValidatorsNodeForm = compose(
  reduxForm({
    form: VALIDATOR_NODE_FORM_ID,
    validate: validate({
      host: [isHost()],
      port: [isNumber(), max(65535), min(0)],
      password: [required()],
    })
  }),
)(ValidatorsNodeForm);

const ValidatorsNodeFrom = ({
  handleCancel,
  handleSubmit,
}) => (
  <Modal
    classNames={{
      container: styles.Container
    }}
    id={VALIDATOR_NODE_FROM_MODAL_ID}
    title=" : "
  >
    <ComposedValidatorsNodeForm
      isFrom
      onCancel={handleCancel}
      onSubmit={handleSubmit}
    />
  </Modal>
);

export const NodeFrom = compose(
  connect(null, { closeModal, openModal }),
  withHandlers({
    handleCancel: ({ closeModal, handleReset }): Function =>
      (event: Object): void => {
        closeModal(VALIDATOR_NODE_FROM_MODAL_ID);
      },
    handleSubmit: ({ closeModal, openModal }): Function => (values) => {
      closeModal(VALIDATOR_NODE_FROM_MODAL_ID);
      openModal(VALIDATOR_NODE_MODAL_ID, { from: {
        host: get(values, 'host', 'localhost'),
        port: get(values, 'port', 22),
        username: get(values, 'username', 'root'),
        password: get(values, 'password'),
      }});
    },
  }),
)(ValidatorsNodeFrom)

const ValidatorsNode: React.Element<Modal> = ({
  error,
  messages,
  progress,
  retry,
  token,
  vPub,
  // Handlers
  handleCancel,
  handleCreateValidator,
  handleReset,
  handleSubmit,
  // State
  isLoading,
  isProcessing,
  isSuccess,
}) => (
  <Modal
    classNames={{
      container: classNames(styles.Container, {
        [styles.ContainerConsole]: isProcessing,
      }),
    }}
    id={VALIDATOR_NODE_MODAL_ID}
    title={isSuccess ? ` ! ${token}` : isProcessing ? ` : ${token}` : ' '}
  >
    {({ from }) => isProcessing ? (
      <Fragment>
        <div className={styles.Console} id="console">
          <div id="wrapper">
           {messages.map(({ date, msg, result }, index) => (
              <div className={styles.Message} key={index}>
                <span className={styles.MessageDate}>
                  [
                  {date.toLocaleString({
                    hour: '2-digit',
                    minute: '2-digit',
                    second: '2-digit',
                  })}]:&nbsp;
                </span>

                {index === messages.length - 1 && retry ? (
                  <Fragment>
                    <span className={styles.Retry}>
                      {`[${get(retry, '1', 0) - get(retry, '0', 0)}/${get(retry, '1', 0)}]`}
                    </span>

                    {progress && (
                      <span className={styles.Progress}>
                        {`[${get(progress, '0', 0)}/${get(progress, '1', 0)}]`}
                      </span>
                    )}
                  </Fragment>

                ) : null}

                <Ansi
                  className={classNames(styles.MessageText, {
                    [styles.MessageTextIsFailure]: result === 'failure',
                    [styles.MessageTextIsSuccess]: result === 'success',
                  })}
                >
                  {msg}
                </Ansi>
              </div>
            ))}

            {error && (
              <div className={styles.Error}>
                [Error]: {error}
              </div>
            )}

            {vPub && (
              <div className={styles.MessagePub}>
                ############################## vPub READY! ###############################<br/>
                ### {vPub} ###<br />
                ##########################################################################
              </div>
            )}
          </div>
        </div>

        {!isLoading && (
          <div className={classNames(styles.Actions, styles.ActionsConsole)}>
            <Button
              color={COLOR.DANGER}
              onClick={handleCancel}
            >
              
            </Button>

            {isSuccess ? (
              <Button
                color={COLOR.PRIMARY}
                onClick={handleCreateValidator}
              >
                 
              </Button>
            ) : (
              <Button
                color={GRADIENT.PURPLE}
                onClick={handleReset}
              >
                  
              </Button>
            )}
          </div>
        )}
      </Fragment>
    ) : (
      <ComposedValidatorsNodeForm
        onCancel={handleCancel}
        onSubmit={(values) => handleSubmit({ ...values, from })}
      />
    )}
  </Modal>
);

export default compose(
  connect(null, { closeModal, openModal }),
  withState('error', 'setError', false),
  withState('isLoading', 'setLoad', false),
  withState('isProcessing', 'setProcess', false),
  withState('isSuccess', 'setSuccess', false),
  withState('messages', 'setMessages', []),
  withState('progress', 'setProgress', null),
  withState('retry', 'setRetry', null),
  withState('token', 'setToken', null),
  withState('vPub', 'setVPub', null),
  withHandlers({
    handleReset: ({ setError, setLoad, setMessages, setProcess, setProgress, setRetry, setSuccess, setVPub }): Function =>
      (event: Object): void => {
        setError(false);
        setLoad(false);
        setMessages([]);
        setProcess(false);
        setProgress(null);
        setRetry(null);
        setSuccess(false);
        setVPub(null);
      },
  }),
  withHandlers({
    handleCancel: ({ closeModal, handleReset }): Function =>
      (event: Object): void => {
        closeModal(VALIDATOR_NODE_MODAL_ID);
        handleReset();
      },
    handleCreateValidator: ({ closeModal, handleReset, openModal, vPub }): Function =>
      (event: Object): void => {
        closeModal(VALIDATOR_NODE_MODAL_ID);
        openModal(VALIDATOR_CREATE_MODAL_ID, { vPub });
        handleReset();
      },
    handleSubmit: ({ setError, setLoad, setMessages, setProcess, setProgress, setRetry, setSuccess, setToken, setVPub }): Function =>
      ({ from, ...values}): Promise =>
        new Promise((resolve: Function, reject: Function) => {
          setProcess(true);

          const messages = [];
          const socket = new WebSocket('wss://api.etherus.org/nd');

          socket.onerror = (error: Object): void => {
            reject(error);
            setLoad(false);
            socket.close();
          }

          socket.onmessage = (event: Object): void => {
            try {
              const { data, msg, result } = JSON.parse(get(event, 'data'));

              if (has(data, 'execution.token')) {
                setToken(get(data, 'execution.token'));
              }

              if (result) {
                setLoad(false);
                socket.close();
                messages.push({ msg, date: DateTime.local(), result });

                if (result === 'success') {
                  resolve();
                  setSuccess(true);
                  setVPub(get(data, 'vPub'));
                } else {
                  setError(get(data, 'err'));
                  reject(msg);
                }
              } else {
                setProgress(get(data, 'progress', null));
                setRetry(get(data, 'retry', null));

                if (has(data, 'retry')) {
                  const retry = get(data, 'retry.1') - get(data, 'retry.0');

                  if (retry === 0) {
                    messages.push({ msg, date: DateTime.local(), result });
                  } else {
                    const lastMessage = last(messages);
                    messages[messages.length - 1] = {
                      ...lastMessage,
                      date: DateTime.local(),
                    };
                  }
                } else {
                  messages.push({ msg, date: DateTime.local(), result });
                }
              }

              setMessages(messages);
            } catch (error) {
              reject(error);
              setLoad(false);
              setProcess(false);
              socket.close();
            }

            const $console = document.getElementById('console');
            const $wrapper = document.getElementById('wrapper');

            $console.scrollTop = $wrapper.clientHeight;
          }

          socket.onopen = (): void => {
            setLoad(true);

            const to = {
              host: get(values, 'host', 'localhost'),
              port: get(values, 'port', 22),
              username: get(values, 'username', 'root'),
              password: get(values, 'password'),
            }

            socket.send(JSON.stringify({
              command: from ? 'migrate' : 'install',
              data: from ? { from, to } : to,
            }));
          }
        })
          .catch((error: Object): void => {
            throw new SubmissionError({ _error: error });
          }),
  }),
)(ValidatorsNode);
